/**
 * 路由表配置
 */
export default [
  {
    path: '*',
    redirect: '/welcome'
  },
  {
    path: '/welcome',
    name: 'Welcome',
    component: resolve => require.ensure([], () => resolve(require('@/components/Welcome.vue')), 'Welcome')
  },
  {
    path: '/blog',
    name: 'Blog',
    component: resolve => require.ensure([], () => resolve(require('@/components/Blog.vue')), 'Blog'),
    children: [
    {
      path: '',
      redirect: {
        name: 'BlogList'
      }
    },
    {
      path: 'list', //注意：这里如果有斜杆，就变成一级地址/#/list，没有就是/#/blog/list
      name: 'BlogList',
      component: resolve => require(['@/components/BlogView/index.vue'], resolve)
    }]
  }
]


